<template>

    <GoogleMap
    api-key="AIzaSyA-eaBXLEzEiYJwnQJy8u0t42yB06vWhRs"
    :style="mStyle"
    :center="center"
    :zoom="10"
    >
        <!-- <Marker :options="{ position: center }" /> -->
    </GoogleMap>   

</template>

<script>
// import { defineComponent } from 'vue'
import { GoogleMap, Marker } from 'vue3-google-map'

export default {    
    components: { 
        GoogleMap, 
        Marker 
    },

    props:{
        height:String,
        width:String,
    },

    setup() {
        const center = { lat: 33.40, lng: 126.58 }
        return { center }
    },

    data() {
        return {
            mStyle: 'width:'+this.width+'; height: '+this.height,
        }
    }
}
</script>